import './Art.css';
import { animated, useSpring } from '@react-spring/web';
import MyNavbar from '../MyNavbar';
import { Button, Container, Row, Col } from 'react-bootstrap';
import { useEffect } from 'react';
import Gallery from './art/Gallery';


function Art() {
  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="Art">
      <MyNavbar active='art' />
      <animated.div style={fadein_props}>
        <h1 className='Title'>ART</h1>

        <Container>
          <Row>
            <Col style={{textAlign: 'left'}}>
              <p>
                This is still pending, but I may open for commissions, but I honestly do not see
                that happening in the near future for now.
              </p>
            </Col>
            <Col>
              <Button id='commissionButton' disabled>COMMISSION FORM</Button>
            </Col>
          </Row>
        </Container>

        <br /><br />
        <hr style={{ color: 'whitesmoke', margin: '1vw' }}></hr>

        <h1>GALLERY</h1>
        <p>
          Full resolution images are available in my Discord server.
        </p>
        <Button id='newgroundsButton' variant='warning' 
          href='https://jessetonic.newgrounds.com/art' target="_blank" rel="noopener noreferrer">
          Check out my full gallery on Newgrounds!
        </Button>
        <br /><br />

        <Gallery/>

      </animated.div>
    </div>
  );
}

export default Art;
