import './ContentRatings.css';
import { animated, useSpring } from '@react-spring/web';
import MyNavbar from '../MyNavbar';
import { Row, Col, Container } from 'react-bootstrap';
import { useEffect } from 'react';


function ContentRatings() {
  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="ContentRatings">
      <MyNavbar active='content_ratings' />
      <animated.div style={fadein_props}>
        <h1 className='Title'>CONTENT RATINGS</h1>

        <p id='introParagraph'>
          Content ratings are present on my content to indicate the approximate age range at
          which a particular content may be suitable or yet too inappropriate for minors of
          a certain age range. 
        </p>

        {/* <Button href="https://drive.google.com/file/d/1bwh_Mh3Q-S3erH11S1dB5RuS7-J8fw-G/view?usp=sharing"
          target="_blank" rel="noopener noreferrer" id='contentRatingGuidelinesButton'>
          CONTENT RATING GUIDELINES
        </Button> */}
        <br />

        <p className='guidelinesList'>
          These quick guidelines may NOT always be interpreted in the most literal manner, as the overall context, tone, message, 
          and psychological impact of the content are important factors in content rating classification.
        </p> <br />

        <Container>
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/04G.png')} alt='G' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>GENERAL</u></h3>
              <p className='ratingSummary'>
                Generally suitable for all ages (4 - 5 and up).
                <ul className='guidelinesList'>
                  <li>A few uses of the mildest profanity (e.g., crap, hell, damn).</li>
                  <li>Limited brief nonsexual nudity. Kissing and embracing.</li>
                  <li>Some mild violence and threat. Short mild scary scenes. Overall reassuring resolution.</li>
                  <li>Adults drinking and/or smoking in the background.</li>
                  <li>Themes sensitive to kids' sense of wellbeing and security. Mild crude content.</li>
                </ul>
              </p>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/08PG.png')} alt='PG' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>PARENTAL GUIDANCE</u></h3>
              <p className='ratingSummary'>
                Parental guidance advised for younger children (under 7 - 9).
                <ul className='guidelinesList'>
                  <li>Mild language. Some bleeped words.</li>
                  <li>Brief nonsexual nudity. Mild suggestive content and innuendo.</li>
                  <li>Stronger violence, especially if cartoony/fantastic. Frightening scenes not sustained or intense.</li>
                  <li>Some drinking and/or smoking. Family-friendly drug use references.</li>
                  <li>More serious themes (e.g., crime, discrimination, abuse, depression), not pessimistic. Crude content.</li>
                </ul>
              </p>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/12+.png')} alt='12+' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>AGES 12+</u></h3>
              <p className='ratingSummary'>
                Not recommended for children under 12.
                <ul className='guidelinesList'>
                  <li>Coarse language, including some uses of "f**k." Infrequent, contextually justified slurs.</li>
                  <li>Nonsexual nudity. Some sexual content and references.</li>
                  <li>Moderate violence and horror. Dangerous behaviour.</li>
                  <li>Drinking and/or smoking. Infrequent drug use or teen drinking/smoking.</li>
                  <li>Mature themes (e.g., drug abuse, sexual harassment, suicide, politics, controversial issues).</li>
                </ul>
              </p>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/15+.png')} alt='15+' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>AGES 15+</u></h3>
              <p className='ratingSummary'>
                Not recommended for minors under 15.
                <ul className='guidelinesList'>
                  <li>Strong language, including discriminatory slurs.</li>
                  <li>Sexual content. Strong sex references.</li>
                  <li>Strong violence and horror. Some gore. Brief, undetailed sexual violence.</li>
                  <li>Teen drinking/smoking and/or drug use.</li>
                </ul>
              </p>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/18+.png')} alt='18+' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>AGES 18+</u></h3>
              <p className='ratingSummary'>
                Suitable for adults 18 and up only.
                <ul className='guidelinesList'>
                  <li>Normalized discriminatory language.</li>
                  <li>Graphic sexual content. Content intended for sexual gratification.</li>
                  <li>Strong focus on brutal violence, torture, cruelty, and/or gore. Graphic sexual violence.</li>
                  <li>Glamorized drug use.</li>
                  <li>Detailed suicide or self-harm.</li>
                  <li>Extremely crude content or behaviour.</li>
                </ul>
              </p>
            </Col>
          </Row><br />
        </Container>

      </animated.div>
    </div>
  );
}

export default ContentRatings;
